@value colors: "../../components/colors.module.css";
@value primaryMain, grey0, grey50, grey100, grey200, grey300, grey400, grey500, grey600, grey700, grey800, grey900 from colors;

.contactSection {
  padding: 40px;
  background-color: grey50;
}

.contactSection form {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 40px;
}

.contactSection form > div {
  display: flex;
  justify-content: space-between;
  margin-bottom: 40px;
  width: 100%;
}

.contactSection form > div > div {
  display: flex;
  flex-direction: column;
  width: calc(50% - 16px);
}

.contactSection form > div > div input:nth-child(2) {
  margin-top: 24px;
}

.contactSection textarea {
  resize: none;
  width: calc(50% - 16px);
}

.contactSection input,
.contactSection textarea {
  box-sizing: border-box;
  border: 0;
  border-bottom: 1px solid grey900;
  padding: 12px;
  background-color: transparent;
  font-family: "Mulish", sans-serif;
  line-height: 16px;
  color: grey900;
}

.contactSection input::placeholder,
.contactSection textarea::placeholder {
  color: grey500;
}
